
<template>
  <div class="container">
    <pre>
    {{input}}
    </pre>
    <textarea type="text" v-model="input"></textarea>
    <div v-bind:"changePage">
      {{showClient}}
    </div>
  </div>
</template>

<script>
  var _clientWidth = document.body.clientWidth
  var _clientHeight = document.body.clientHeight
  
  var vueobj = {
    data () {
      return {
        input: '',
        clientWidth: _clientWidth,
        clientHeight: _clientHeight
      }
    },
    methods: {
      changePage: function (clientWidth, clientHeight) {
        this.clientWidth = clientWidth
        this.clientHeight = clientHeight
      }
    },
    computed: {
      showClient: function () {
        console.log(1111, this.clientWidth, this.clientHeight)
        return `clientWidth: ${this.clientWidth} ; clientHeight: ${this.clientHeight}`
      }
    }
  }
  window.onresize = function (e) {
    var clientWidth = document.body.clientWidth
    var clientHeight = document.body.clientHeight
    vueobj.methods.changePage(clientWidth, clientHeight)
  }
  export default vueobj
</script>

<style lang='less' >
  .container {
    textarea {
      border: 1px solid #e9e9e9;
    }
  }
</style>


