<template>
  <div class="container">
    <h1>{{title}}</h1>
  </div>  
</template>

<script type="text/javascript">
  export default {
    data () {
      return {
        title: '开始了一段奇幻之旅'
      }
    }
  }
</script>


<!-- scoped 表示css的所有只在本域中起作用  -->
<style lang='less' scoped>
  .container {
    font-size: 16px;
    font-family: "-apple-system,PingFang SC,Hiragino Sans GB,Arial,Microsoft YaHei,Helvetica Neue,sans-serif;";
    h1 {
      font-size:1.2em;
      text-shadow: .2em .2em rgba(0,0,0,.4);
    }
  }

</style>



